.App {
  text-align: center;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
}

.App-logo {
  width: 100px;
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
}

.App-header {
  min-width: 170px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
  background: #222;
  padding: 10px 0;
}

.App-router {
  text-decoration: none;
  text-align: center;
  padding: 10px 0;
  margin: 0;
}

.App-router li {
  line-height: 1.8;
}

.App-router li a {
  text-decoration: none;
  color: #dfdfdf;
  font-size: 14px;
}

.App-router li a.active {
  font-weight: bold;
  border-bottom: 1px solid #dfdfdf;
}

.App-main {
  margin: 40px 0 0 40px;
  text-align: center;
  width: 100%;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: column;
      flex-direction: column;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

/* sample form */

.sample-form-container {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-direction: row;
      flex-direction: row;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-pack: center;
      justify-content: center;
}

.sample-form {
  padding: 0;
  margin: 0;
  width: 70%;
}

.form-logger {
  padding: 0;
  margin: 0;
  margin-top: -40px;
  background: #aaa;
  color: white;
  width: 30%;
  text-align: left;
  min-height: 800px;
}

.form-logger li {
  list-style: none;
  margin: 4px 12px 0;
}



